<template>
<el-container>
<el-header width="250px">
  <h5>1653014 李晓恒 内存管理项目 动态内存分配</h5>
</el-header>

<el-main >
<el-container>
<el-card class="box-card" >
  <div slot="header" class="clearfix">
    <span>请求分配队列</span>
  </div>
  <el-table :data="order" height="300px">
    <el-table-column prop="num" label="进程号" width="80">
        </el-table-column>
        <el-table-column prop="order" label="申请/释放" width="80">
        </el-table-column>
        <el-table-column prop="room" label="内存大小（K）">
        </el-table-column>
  </el-table>
  
  <formm v-on:clear='Clear' @add='Add' @cut='Cut' ref="formm">
  </formm>

</el-card>

<el-card class="box-card" >
  <div slot="header" class="clearfix">
    <span>控制器</span>
  </div>

  <el-form>
  <el-form-item label="选择算法">
    <el-select v-model="meth" placeholder="请选择" :disabled=bable>
      <el-option label="最先适配算法" value="first"></el-option>
      <el-option label="最佳适配算法" value="best"></el-option>
    </el-select>
  </el-form-item>
  </el-form>

  <el-button :disabled=able type="primary" @click="runOrder(order[0].num,order[0].room,order[0].order)">执行一条指令</el-button>
  <el-button type="primary" @click="refresh()">重置</el-button>
  <br>
  <br>
  <span>先选择算法才能开始执行</span>
  <br>
  <span>开始执行指令后将不能修改指令队列</span>
</el-card>

<el-card class="box-card" >
  <div slot="header" class="clearfix">
    <span>内存情况</span>
  </div>
  <span>申请/分配记录</span>
  <el-table :data="ordered" height="150px">
        <el-table-column prop="num" label="进程号" width="80">
        </el-table-column>
        <el-table-column prop="order" label="申请/释放" width="80">
        </el-table-column>
        <el-table-column prop="room" label="内存大小（K）">
        </el-table-column>
  </el-table>
  
    <span>空闲分区链情况(红色）</span>
  
  <el-table :data="empty" height="250px">
        <el-table-column prop="start" label="起始" width="80">
        </el-table-column>
        <el-table-column prop="end" label="结束" width="80">
        </el-table-column>
        <el-table-column prop="size" label="内存大小（K）">
        </el-table-column>
  </el-table>
    <span>已占用内存情况（蓝色）</span>
  
  <el-table :data="full" height="250px">
        <el-table-column prop="start" label="起始" width="80">
        </el-table-column>
        <el-table-column prop="end" label="结束" width="80">
        </el-table-column>
        <el-table-column prop="size" label="内存大小（K）">
        </el-table-column>
  </el-table>

</el-card>

<el-card class="box-card2" >
   <div slot="header" class="clearfix">
    <span >内存情况展示</span>
    <br>
    <span>从下到上地址依次为1-640</span>
  </div>
  <store ref="store">
  </store>
</el-card>
</el-container>
</el-main>

</el-container>
</template>
  


<script>
import store from './components/part.vue'
import formm from './components/form.vue'


  export default {
    components: {
    store,
    formm
  },
    data() {
      const item = {
        date: '2016-05-02',
      };
      return {
        bable:false,
        able:true,
        order:[
          {num:1,order:'申请',room:130},
          {num:2,order:'申请',room:60},
          {num:3,order:'申请',room:100},
          {num:2,order:'释放',room:60},
          {num:4,order:'申请',room:200},
          {num:3,order:'释放',room:100},
          {num:1,order:'释放',room:130},
          {num:5,order:'申请',room:140},
          {num:6,order:'申请',room:60},
          {num:7,order:'申请',room:50},
          {num:6,order:'释放',room:60},
        ],
        
        current:8,
        meth:'',
        ordered:[],
        empty:[],
        full:[],
        tableData: Array(20).fill(item)
      }
    },
    watch:{
      meth:function(){
        if(this.meth!='')
        {
          this.able=false
        }
      },
      order:function(){
        let tempord=[]
        for(let orders of this.order)
        {
          let count = 0
          for(let orderr of this.order)
          {
            if(orderr.num==orders.num)
            {
              count++
            }
          }
          if(count==1)
          {
            tempord.push(orders)
          }
        }
        this.$refs.formm.ord=tempord
      }
    },
    methods: {
      Cut(){
        let a = Number(this.$refs.formm.cutNum)
        let b
        for(let ord of this.order)
        {
          if(ord.num==a)
          {
            b=ord
            break
          }
        }
        this.order.push({num:a,order:'释放',room:b.room})
        this.$refs.formm.form.nun=''
      },
      Add(){
        let a =Number(this.$refs.formm.form.sizz)
        this.order.push({num:this.current,order:'申请',room:a})
        this.current++
        return
      },
      Clear(){
        this.order.splice(0,this.order.length)
        this.current=1
        return
      },
      runOrder(num,room,a) {
            console.log("父组件中调用子组件方法");
            if(this.meth=="best")
            {
              this.$refs.store.bestRun(num,room,a)
            }
            else if(this.meth=="first")
            {
              this.$refs.store.runOrder(num,room,a) 
            }
            this.ordered.push(this.order.shift())
            this.empty=this.$refs.store.empty
            this.full=this.$refs.store.full
            this.bable=true
            this.$refs.formm.diabe()
            return
        },
      refresh(){
        window.location.reload()
      }
    },
  }
</script>

