<template>
  <div class="container">
      <div v-for="(n,index) in storage" :key="index"  >   
          <div class="pix" :class="{'active':n.state===true}">

          </div>
      </div>
  </div>
</template>

<script type="text/ecmascript-6">
function item(address){
          this.address=address;
          this.state=false;
        }
function node(a,b,n){
          this.start=a;
          this.end=b;
          this.size=b-a+1;
          this.num=n;
        }


export default {
    name:'store',
    data(){
       
        
        return{
        // 数据
        empty:[],
        full:[],
        storage: []
        };
    },
    components:{
      // 组件注册 如果此页面有使用到components组件的话，需要导入组件包，并在此进行注册
     
    },
    beforeCreate(){
      
      // 在实例初始化之后，数据观测(data observer) 和 event/watcher 事件配置之前被调用。
    
    },
    created(){
      this.empty.push(new node(1,640,0))

      let i=0;
      for(i=0;i<640;i++){
        this.storage.push(new item(640-i));
        console.log(this.storage[i].address)
      }

      
      // 实例已经创建完成之后被调用。在这一步，实例已完成以下的配置：数据观测(data observer)，属性和方法的运算， watch/event 事件回调。然而，挂载阶段还没开始，$el 属性目前不可见。
    },
    beforeMount(){
      // 在挂载开始之前被调用：相关的 render 函数首次被调用。
    },
    mounted(){
      // el 被新创建的 vm.$el 替换，并挂载到实例上去之后调用该钩子。
    },
    beforeUpdate(){
      // 数据更新时调用，发生在虚拟 DOM 重新渲染和打补丁之前。 你可以在这个钩子中进一步地更改状态，这不会触发附加的重渲染过程。
    },
    updated(){
      // 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子。
      // 当这个钩子被调用时，组件 DOM 已经更新，所以你现在可以执行依赖于 DOM 的操作。然而在大多数情况下，你应该避免在此期间更改状态，因为这可能会导致更新无限循环
    },
    beforeDestroy(){
      // 实例销毁之前调用。在这一步，实例仍然完全可用。 
    },
    destroyed(){
      // Vue 实例销毁后调用。调用后，Vue 实例指示的所有东西都会解绑定，所有的事件监听器会被移除，所有的子实例也会被销毁。 该钩子在服务器端渲染期间不被调用。
    },
    computed:{
      // 计算属性
    },
    watch:{
      // 数据监听
      full:function(){
          console.log('changedd')
          this.ssort()
          this.merge()
          for(let fu of this.full)
          {
            console.log(fu.start)
            console.log(fu.end)
            for(let i=fu.start;i<=fu.end;i++)
            {
              this.storage[640-i].state=true
            }
          }
          console.log('yes')
          for(let em of this.empty)
          {
            console.log(em.start)
            console.log(em.end)
            for(let i=em.start;i<=em.end;i++)
            {
              this.storage[640-i].state=false
            }
          }
          
          return
        },
        deep:true,
    
    },
    methods:{
      // 方法定义
      bestRun(numm,room,order){
        console.log(numm)
        console.log(typeof(room) )
        console.log(order)
        if(order=='申请')
        {
          console.log('B申请')  
          let best=999
          let temp=-1
  
          for(let i in this.empty)
          {
            if(this.empty[i].size>=room)
            {
              let val=this.empty[i].size-room
              if(val<best)
              {
                best=val
                temp=i
              }
            }
          }
          this.full.push(new node(this.empty[temp].start,this.empty[temp].start+room-1,numm))
              this.empty[temp].size-=room;
              this.empty[temp].start+=room; 
        }
        else if(order=='释放')
        {
          console.log('释放')
          for(let index in this.full)
          {
            if(this.full[index].num==numm)
            {
               console.log('释放1')
              this.empty.push(this.full[index])
                console.log('释放2')
              this.full.splice(index,1)
              break
            }
          }
        }
        this.ssort()
        this.merge()
        return
      },

      runOrder(numm,room,order){
        console.log(numm)
        console.log(typeof(room) )
        console.log(order)
        if(order=='申请')
        {
          let succ=false
          console.log('申请')  
          for(let nodes of this.empty)
          {
            if(nodes.size>=room)
            {
              this.full.push(new node(nodes.start,nodes.start+room-1,numm))
              nodes.size-=room;
              nodes.start+=room;
              succ=true
              break
            }
          }
          if(succ==false)
          {
            this.open()
          }
        }
        else if(order=='释放')
        {
          console.log('释放')
          for(let index in this.full)
          {
            if(this.full[index].num==numm)
            {
               console.log('释放1')
              this.empty.push(this.full[index])
              
                console.log('释放2')
              this.full.splice(index,1)
              break
            }
          }
        }
        this.ssort()
        this.merge()
        return
      },

      open() {
        this.$confirm('没有足够的内存可以支配, 即将重启', '内存溢出', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          window.location.reload()
          
          
        }).catch(() => {
          window.location.reload()
          
        });
      },

        // window.location.reload()
      

      ssort(){
        
        for(let i = 0;i<this.full.length;i++)
        {
          for(let j =0;j<this.full.length-i-1;j++)
          {
            if(this.full[j].start>this.full[j+1].start)
            {
              let tmp =this.full[j]
              this.full[j]=this.full[j+1]
              this.full[j+1]=tmp
            }
          }
        }
        
        for(let i = 0;i<this.empty.length;i++)
        {
          for(let j =0;j<this.empty.length-i-1;j++)
          {
            if(this.empty[j].start>this.empty[j+1].start)
            {
              let tmp =this.empty[j]
              this.empty[j]=this.empty[j+1]
              this.empty[j+1]=tmp
            }
          }
        }
      },
      merge(){
        
        for(let i = 0;i<this.empty.length;i++)
        {
          this.empty[i].num=0;
        }
        for(let i = 0;i<this.empty.length-1;i++)
        {
          if((this.empty[i].end+1)==this.empty[i+1].start)
          {
            this.empty[i].end=this.empty[i+1].end
            this.empty[i].size+=this.empty[i+1].size
            this.empty.splice(i+1,1)
          }
        }
      }

    },
}
</script>

<style>
 .pix{
   width: 250px;
   height: 1px;
   background-color: rgb(238, 9, 9);
   display: block;
 }

 .active{
  background-color: rgb(13, 34, 223)!important;
 }
</style>
